import axios from 'axios';

const api = axios.create({
	baseURL: 'https://api.themoviedb.org/3/',
});

// api.get('tv/popular', {
// 	params: {
// 		api_key: '2974fd09ab243180d8433035f0f0c0f3',
// 		language: 'en-US',
// 	},
// });

api.interceptors.request.use((config) => {
	config.params = config.params || {};
	config.params['api_key'] = '2974fd09ab243180d8433035f0f0c0f3';
	config.params['language'] = 'en-US';
	return config;
});

export const moviesApi = {
	nowPlaying: () => api.get('movie/now_playing'),
	upcoming: () => api.get('movie/upcoming'),
	popular: () => api.get('movie/popular'),
	movieDetail: (id) => api.get(`movie/${id}`, { prams: { append_to_response: 'videos' } }),
	search: (term) => api.get('search/movie', { params: { query: encodeURIComponent(term) } }),
};
export const tvApi = {
	topRated: () => api.get('tv/top_rated'),
	airingToday: () => api.get('tv/airing_today'),
	popular: () => api.get('tv/popular'),
	showDetail: (id) => api.get(`tv/${id}`, { prams: { append_to_response: 'videos' } }),
	search: (term) => api.get('search/tv', { params: { query: encodeURIComponent(term) } }),
};

export default api;
